.slect{font-size:12px}.slect-input{height:100%;width:100%;z-index:1;display:inline-block;padding:8px;overflow:hidden;border:1px solid #d0d0d0;-webkit-border-radius:3px;-moz-border-radius:3px;border-radius:3px;-webkit-box-shadow:inset 0 1px 1px rgba(0,0,0,.1);-moz-box-shadow:inset 0 1px 1px rgba(0,0,0,.1);box-shadow:inset 0 1px 1px rgba(0,0,0,.1);-webkit-box-sizing:border-box;-moz-box-sizing:border-box;box-sizing:border-box}.slect-input:focus{outline:none}.slect-chevron-container{display:inline-block;width:13px;height:13px;position:absolute;top:50%;-webkit-transform:translateY(-50%);-moz-transform:translateY(-50%);-ms-transform:translateY(-50%);-o-transform:translateY(-50%);transform:translateY(-50%);right:6px;cursor:pointer}.slect-suggestion-list{display:none;max-height:150px;overflow:auto;position:absolute;left:0;top:100%;min-width:100%;z-index:10;margin:0;background:#fff;border:1px solid #d0d0d0;border-top:0;-webkit-border-radius:0 0 3px 3px;-moz-border-radius:0 0 3px 3px;border-radius:0 0 3px 3px;-webkit-box-shadow:0 1px 3px rgba(0,0,0,.1);-moz-box-shadow:0 1px 3px rgba(0,0,0,.1);box-shadow:0 1px 3px rgba(0,0,0,.1);-webkit-box-sizing:border-box;-moz-box-sizing:border-box;box-sizing:border-box}.slect-suggestion-list-item{position:relative;padding:10px 8px;border-bottom:.5px solid #ededed;cursor:pointer;font-family:sans-serif}.slect-suggestion-list-item:last-child{border-bottom:none}.slect-suggestion-list-item:hover{color:#495c68;background-color:#f5fafd}.slect-suggestion-list-item.selected{color:#495c68;background-color:#f5fafd}.slect-suggestion-list-item.selected .slect-checkmark-container{display:inline-block}.slect-checkmark-container{display:none;width:13px;height:13px;position:absolute;top:50%;-webkit-transform:translateY(-50%);-moz-transform:translateY(-50%);-ms-transform:translateY(-50%);-o-transform:translateY(-50%);transform:translateY(-50%);right:6px;cursor:pointer}